import { mapActions, mapGetters } from 'vuex'
import { cloudFunction } from '@/utils/request'


export default {
  name: 'AuthDialog',
  props: {
    isShow: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      handleLoading: false,
      showClubList: false,
      isBreak: false,
      isShowApply: false,
      bindSchoolStatus: 0,
      clubList: [],
      year: null,
      yearList: [2019, 2018, 2017, 2016, 2015, 2014, 2013, 2012, 2011, 2010],
      yearIndex: -1,
      selectClub: null,
      step: 0,
      stepList: [
        {
          name: 'QQ授权'
        },
        {
          name: '信息确认'
        },
        {
          name: '完成'
        }
      ],
    }
  },
  computed: {
    ...mapGetters('user', ['isAuth', 'userInfo', 'club', 'hasBindClub', 'hasBindSchool'])
  },
  created: async function () {
    this.isAuth && this.getUserInfo(), this.initStep()
  },
  methods: {
    ...mapActions('user', [
      'getUserInfo',
      'authUserInfo',
    ]),
    // 初始化步骤条
    initStep () {
      let step = 0
      if (!this.isAuth) this.step = 0
      else if (!this.hasBindClub) {
        this.step = 1
      }
      else if (!this.hasBindSchool) {
        this.step = 2
        this.bindSchoolStatus = 1
      } else {
        // 33 大结局
        this.step = 3
        this.bindSchoolStatus = 3
      }
    },
    // 获取用户信息
    async bindGetUserInfo (e) {
      uni.showLoading({ title: '正在获取...' })
      try {
        const res = await this.authUserInfo(e)
        await this.getUserInfo()
        this.initStep()
        uni.hideLoading()
      } catch (error) {
        uni.hideLoading()
      }
    },
    // 设置社团列表
    async setClubListShow () {
      uni.showLoading({ title: '加载中...' })
      await this.getClubList()
      this.step = 1
      this.showClubList = true
      uni.hideLoading()
    },
    // 获取社团列表
    getClubList () {
      cloudFunction('getClubList', {
        status: 0
      }).then(res => {
        const [err, data] = res
        if (data.code === 0) {
          this.clubList = data.data
        }
      })
    },
    // 设置社团绑定
    setBindClub (club) {
      this.selectClub = club
      this.isShowApply = true
    },

    // 社团申请
    async clubApply (id, userInfo) {
      if (!/^(1949|19[5-9]\d|20\d{2}|2100)$/.test(this.year)) {
        uni.showToast({ icon: 'none', title: '请输入四位数年份' })
      } else {
        this.handleLoading = true
        const res = await cloudFunction('applyClub', { clubId: this.selectClub.id, userInfo, year: this.year })
        const [err, data] = res
        if (data.code === 0) {
          this.setFinishStatus(2)
        }
        await this.getUserInfo()
        this.handleLoading = false
        this.isShowApply = false
      }
    },

    // 登录学校
    async formSubmit (e) {
      uni.showLoading({ title: '登录中...' })
      const { studentId, password } = e.mp.detail.value
      const user = await qq.BaaS.auth.getCurrentUser()
      const res = await user.set({
        'studentId': studentId,
        'password': password,
        'schoolId': 1
      }).update()
      if (res.statusCode === 200) {
        await this.getUserInfo()
        this.setFinishStatus(3, 3)
      }
      uni.hideLoading()
    },

    // 完成之后的操作
    setFinishStatus (step, finishStatus, isBreak) {
      if (isBreak) this.isBreak = true
      console.log(step, this.hasBindSchool)
      if (step === 2 && this.hasBindSchool) {
        // 处理绑定过学校, 而没有绑定社团的
        this.step = 3
        this.bindSchoolStatus = 3
      } else {
        this.step = step
        this.bindSchoolStatus = finishStatus
      }
    },
    // 隐藏
    setDialogHidden () {
      this.$emit('callback', { isAuth: this.isAuth, hasBindClub: this.hasBindClub })
      // uni.redirectTo({
      //   url: '/pages/finder/index'
      // })
    },
    // 设置模态框隐藏
    setModalHide () { this.isShowApply = false },
    setYearChange (e) { this.yearIndex = +e.detail.value }
  }
}
